import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, AlertController } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
import { DatabaseProvider } from '../../providers/database/database';
import { LangModalPage, PicPage } from '../../pages/pages';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Storage } from '@ionic/storage';
import { Globalization } from '@ionic-native/globalization';
import { appSettings } from '../../app/app-settings';

/**
 * Generated class for the HomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
	selector: 'page-home',
	templateUrl: 'home.html',
})
export class HomePage {
	loaded: boolean = false;
	picPage = PicPage;
	//achatPage = AchatPage;
	public cats: any[];
	achat: any;

	constructor(public globalization: Globalization, public storage: Storage, private iab: InAppBrowser, public alertCtrl: AlertController, public translate: TranslateService, public navCtrl: NavController, private database: DatabaseProvider, public modalCtrl: ModalController) {
		storage.ready().then(() => {
			storage.get('firstLaunch').then((val) => {
				if (!val || val === null) {
					storage.set('firstLaunch', true);
					//détection de la langue
					if ((<any>window).cordova) {
						this.globalization.getPreferredLanguage().then(result => {
							let res = result.value.split("-");
							//verif si existe dans liste des langues (interne)
							this.checkLanguage(res[0]);
						}).catch(e => alert(e));
					} else {
						this.checkLanguage(appSettings.LANGUAGE_DEFAULT);
					}
				} else {

					storage.get('lang').then((val) => {
						translate.use(val);
					});
				}
				//Besoin d'attendre que le constructeur de picData ait bien intégré toute les data
				this.loadData();
			});
		});
	}

	checkLanguage(lang) {
		let founded = false;
		for (let i = 0; i < appSettings.LANGUAGE_LIST.length; i++) {
			if (appSettings.LANGUAGE_LIST[i] === lang) {
				this.storage.set('lang', lang);
				this.translate.use(lang);
				founded = true;
				break;
			}
		}
		if (!founded) {
			this.storage.set('lang', appSettings.LANGUAGE_DEFAULT);
			this.translate.use(appSettings.LANGUAGE_DEFAULT);
		}
	}

	loadData() {
		this.database.load()
			.then(data => {
				this.cats = data;
				this.loaded = true;
			});
	}

	openModal() {
		let languageModal = this.modalCtrl.create(LangModalPage);
		languageModal.present();
	}

	goToPicPage(pageId) {
		this.navCtrl.push(this.picPage, {
			"pageId": pageId,
		});
	}

	goToExternalLink(param) {
		let link;
		switch (param) {
			case "T":
				link = "https://twitter.com/wordtrotterapp";
				break;
			case "F":
				link = "https://www.facebook.com/wordtrotterapp";
				break;
			case "I":
				link = "https://www.instagram.com/wordtrotter/";
				break;
			case "M":
				link = "mailto:contact@word-trotter.com";
				break;
			default:
				link = "mailto:contact@word-trotter.com";
				break;
		}
		this.iab.create(link, '_system');
	}

}



